'use client';
import React, { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/app/components/ui/button';
import { Input } from '@/app/components/ui/input';
import { ScrollArea } from '@/app/components/ui/scroll-area';
import { Send, User, Sparkles, Loader2, RefreshCw } from 'lucide-react';

interface Message {
  id: string;
  content: string;
  type: 'user' | 'ai';
  timestamp: Date;
  isLoading?: boolean;
}

const ChatPage: React.FC = () => {
  const router = useRouter();
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState<string>('');
  const [loading, setLoading] = useState<boolean>(false);
  const [initializing, setInitializing] = useState<boolean>(true);
  const [conversationId, setConversationId] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement | null>(null);
  
  const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:5000';
  
  // Start a new conversation
  const startNewConversation = async () => {
    setLoading(true);
    setMessages([]);
    
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        router.push('/auth/login');
        return;
      }
      
      const response = await fetch(`${API_BASE_URL}/api/chat/start`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({})
      });
      
      if (!response.ok) {
        if (response.status === 401) {
          router.push('/auth/login');
          return;
        }
        throw new Error(`Failed to start conversation: ${response.status}`);
      }
      
      const data = await response.json();
      setConversationId(data.conversation_id);
      
      setMessages([{
        id: '1',
        content: data.initial_response || "Welcome to your astrology consultation. How can I help you today?",
        type: 'ai',
        timestamp: new Date()
      }]);
      
    } catch (error) {
      console.error('Error starting conversation:', error);
    } finally {
      setLoading(false);
      setInitializing(false);
    }
  };
  
  // Initialize chat
  useEffect(() => {
    startNewConversation();
  }, []);
  
  // Auto-scroll to bottom when messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);
  
  const handleSendMessage = async () => {
    if (!input.trim() || loading || !conversationId) return;
    
    // Add user message
    const userMessage: Message = {
      id: Date.now().toString(),
      content: input,
      type: 'user',
      timestamp: new Date()
    };
    
    // Add a placeholder for the AI response
    const placeholderId = (Date.now() + 1).toString();
    
    setMessages(prev => [...prev, userMessage, {
      id: placeholderId,
      content: '',
      type: 'ai',
      timestamp: new Date(),
      isLoading: true
    }]);
    
    const userInput = input;
    setInput('');
    setLoading(true);
    
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        throw new Error('No authentication token found');
      }
      
      const response = await fetch(`${API_BASE_URL}/api/chat/continue`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          message: userInput,
          conversation_id: conversationId
        })
      });
      
      if (!response.ok) {
        if (response.status === 401) {
          router.push('/auth/login');
          return;
        }
        throw new Error(`Request failed: ${response.status}`);
      }
      
      const data = await response.json();
      
      // Replace placeholder with actual response
      setMessages(prev => 
        prev.map(msg => 
          msg.id === placeholderId ? {
            ...msg,
            content: data.response || "I'm sorry, I couldn't generate a response.",
            isLoading: false
          } : msg
        )
      );
      
    } catch (err) {
      console.error('Error sending message:', err);
      
      // Replace placeholder with error message
      setMessages(prev => 
        prev.map(msg => 
          msg.id === placeholderId ? {
            ...msg,
            content: `Error: ${err instanceof Error ? err.message : "Unknown error"}. Please try again.`,
            isLoading: false
          } : msg
        )
      );
    } finally {
      setLoading(false);
    }
  };
  
  // Handle enter key press
  const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };
  
  if (initializing) {
    return (
      <div className="h-screen flex items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-purple-600" />
      </div>
    );
  }
  
  return (
    <div className="flex flex-col h-screen">
      <div className="bg-white border-b p-4 flex items-center justify-between">
        <div className="flex items-center">
          <div className="bg-purple-100 p-2 rounded-full mr-3">
            <Sparkles className="h-5 w-5 text-purple-600" />
          </div>
          <h1 className="text-xl font-semibold">Astrology Chat</h1>
        </div>
        
        <Button 
          variant="outline" 
          size="sm" 
          onClick={startNewConversation}
          disabled={loading}
          className="text-xs flex items-center"
        >
          <RefreshCw className="h-3 w-3 mr-1" />
          New Consultation
        </Button>
      </div>
      
      <div className="flex-1 overflow-hidden">
        <ScrollArea className="h-full p-4">
          <div className="space-y-4">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
              >
                <div 
                  className={`flex items-start space-x-2 max-w-[75%] ${
                    message.type === 'user' ? 'flex-row-reverse space-x-reverse' : ''
                  }`}
                >
                  <div className={`p-2 rounded-full ${
                    message.type === 'user' ? 'bg-purple-100' : 'bg-blue-100'
                  }`}>
                    {message.type === 'user' ? 
                      <User className="h-4 w-4 text-purple-600" /> : 
                      <Sparkles className="h-4 w-4 text-blue-600" />
                    }
                  </div>
                  <div 
                    className={`p-3 rounded-lg ${
                      message.type === 'user' 
                        ? 'bg-purple-600 text-white' 
                        : 'bg-white border border-gray-200'
                    }`}
                  >
                    {message.isLoading ? (
                      <div className="flex items-center space-x-2">
                        <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce"></div>
                        <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-150"></div>
                        <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-300"></div>
                      </div>
                    ) : (
                      <div dangerouslySetInnerHTML={{ 
                        __html: message.content.replace(/\n/g, '<br>') 
                      }} />
                    )}
                  </div>
                </div>
              </div>
            ))}
            <div ref={messagesEndRef} />
          </div>
        </ScrollArea>
      </div>
      
      <div className="border-t bg-white p-4">
        <div className="flex space-x-2">
          <Input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyPress}
            placeholder="Type your message..."
            disabled={loading}
            className="flex-1"
          />
          <Button 
            onClick={handleSendMessage}
            disabled={loading || !input.trim() || !conversationId}
            className="bg-purple-600 hover:bg-purple-700"
          >
            {loading ? (
              <Loader2 className="h-5 w-5 animate-spin" />
            ) : (
              <Send className="h-5 w-5" />
            )}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default ChatPage;